import axios from "axios";

const URL = "https://app.exo.sc/api";
const token = "";

const getData = async adress => {
  try {
    const response = await axios.get(`${adress}`, {
      headers: {
        "Content-Type": "application/json;charset=UTF-8",
        "Access-Control-Allow-Origin": "*",
        "X-Timezone": "Asia/Irkutsk",
        "Authorization": `Bearer ${token}`
      }
    });

    return response.data;
  } catch (err) {
    throw new Error(`${err.status}`);
  }
};

const episodes = {
  getList(count,page) {
    return getData(`${URL}/episodes?count=${count}?page=${page}`);
  },
  getEpisode(id) {
    return getData(`${URL}/episodes/${id}?with=previous,next,blocks,media`);
  }
};

export { episodes };



