<template lang="pug">
  .article
    .article__top
      .container
        .article__logo
        .article__balls
          .imgBall.ball1
          .imgBall.ball2
          .imgBall.ball3
    section.article__content
      .container
        .article__info
          .article__tag Episode {{ episodeData.id }}
          h1.article__title {{ episodeData.name }}
          h4.article__subtitle Обзор значимых событий криптовалютного мира с 20 по 27 февраля 2017 года
          .share
            a.share__link.vk Поделиться
            a.share__link.fb Поделиться
            a.share__link.tg Отправить
            a.share__link.tw Твитнуть
        .article__block(v-for="(block,index) in episodeData.blocks" :key="index")
          h2.article__block__title {{ block.title }}
            span(:class="block.subtitle.type") 
              img(v-if="block.subtitle.meta" :src="block.subtitle.meta.icons.color")
              | {{ block.subtitle.text }}
          .article__block__text(v-html="block.content")
          .comment.article__comment
            .comment__content(v-html="block.comment.text")
            .comment__rate(:class="block.comment.rate") {{ block.comment.title }}
            .comment__img(:class="block.comment.rate")
        .article__last
          .article__last__text Если вам понравилось, то что вы прочитали… Пожалуйста лайк-шэр-алишер.
          .share
            a.share__link.vk Поделиться
            a.share__link.fb Поделиться
            a.share__link.tg Отправить
            a.share__link.tw Твитнуть
        .article__pagination   
          .article__pagination__container       
            .article__pagination__item(v-if="prevData.published_identifier" @click="routeTo(`/episode/${prevData.id}`)")
              .article__pagination__item__img            
              .article__pagination__item__info
                .article__pagination__item__tag Episode {{ prevData.id }}
                h3.article__pagination__item__title {{ prevData.name }}
                .article__pagination__item__time {{ prevData.created_at_diff }}
                .article__pagination__item__duration 10 min
            .article__pagination__text(v-else) Раньше эпизодов не было.
          .article__pagination__container 
            .article__pagination__item(v-if="nextData.published_identifier" @click="routeTo(`/episode/${nextData.id}`)")
              .article__pagination__item__img            
              .article__pagination__item__info
                .article__pagination__item__tag Episode {{ nextData.id }}
                h3.article__pagination__item__title {{ nextData.name }}
                .article__pagination__item__time {{ nextData.created_at_diff }}
                .article__pagination__item__duration 10 min
            .article__pagination__text(v-else) Больше эпизодов нет.
        .more-btn__container(@click="routeTo('/all-episodes')")
          button.more-btn            
            span.dots 
            | Все эпизоды
          
</template>

<script>
import Episodes from '@/partials/Episodes.vue';
import { episodes } from '@/API';

export default {
  name: "episode",
  components: {
    Episodes,
  },
  created(){
    this.getData();
  },
  watch: {
    '$route.params.id' () {
      this.getData();
    }
  },
  data() {
    return {
      episodeData: [],
      nextData: {},
      prevData: {},
    }
  },
  methods: {
    async getData() {
      const data = await episodes.getEpisode(this.$route.params.id);
      this.episodeData = data.data;
      this.nextData = data.data.next;
      this.prevData = data.data.previous;
      console.log(data);
    },
    routeTo(path) {
      this.$router.push(path);
    },
  }
};
</script>



