import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import Episode from "./views/Episode.vue";
import AllEpisodes from "./views/AllEpisodes.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/episode/:id",
      name: "episode",
      component: Episode
    },
    {
      path: "/all-episodes",
      name: "all-episodes",
      component: AllEpisodes
    }
  ]
});



