<template lang="pug">
  section.episode
    .container
      .episode__container
        .episode__block(
          v-for="(el, index) in episodeData"
          @click="routeTo(`/episode/${el.id}`)"
          :key="index")
          .episode__img            
          .episode__info
            .episode__tag {{ `Эпизод ${el.id}` }}
            h3.episode__title {{ el.name }}
            .episode__time {{ el.created_at_diff }}
            .episode__duration {{ parseMin(el.read_time) }}
        a(href="/" target="_blank").episode__add Ad block 
          img(src="../assets/static/banner.png") 
        .more-btn__container(@click="routeTo('/all-episodes')")
          button.more-btn            
            span.dots 
            | Все эпизоды
</template>

<script>
import { episodes } from '@/API';

export default {
  name: "Episodes",
  data() {
    return {
      episodeData: []
    }
  },
  mounted(){
    this.loadEpisode();
  },
  methods: {
    async loadEpisode() {
      const data = await episodes.getList(3,1);      
      this.episodeData = data.data;
    },
    parseMin(time){
      if(time < 60) {
        return `${time} секунд`
      }
      const min = Math.floor(time / 60);
      return `${min} мин`;
    },
    routeTo(path) {
      this.$router.push(path);
    },
  },
};
</script>



